/* Home */
.titleBox{
  /* height: 100vh; */
  text-align: center;
  /* display: grid; */
  /* place-items: center; */
  position: relative;
}
.typing{
  width: 100%;
  animation: typing 2s steps(22), blink .5s step-end infinite alternate;
  white-space: nowrap;
  overflow: hidden;
  border-right: 3px solid;
  font-size: 4em;
  font-weight: bold;
}
.typing span{
  background-color: yellowgreen;
  color: #fff;
}
.subText{
  font-size: 1.2em;
}
.subText>p{
  line-height: 2.5;
}
.cursorIcon{
  position: absolute;
  right: 28%;
  top: 53%;
  width: 30px;
  height: 30px;
}
.cursorIcon>img{
  width: 100%;
}
.copy{
  position: absolute;
  bottom: 50px;
  right: 50%;
  transform: translateX(50%);
}
.copy>small{
  font-size: 1.1em;
  font-family: sans-serif;
}
.copy>small::after{
  content: "";
  display: block;
  position: absolute;
  width: 25px;
  height: 25px;
  left: -25px;
  top: -7px;
  background: url(/src/images/pin-icon.png);
  background-position: center;
  background-size: 100%;
}
@keyframes typing {
  from {
    width: 0
  }
}
    
@keyframes blink {
  50% {
    border-color: transparent
  }
}

















