.skillWrap{
    display: flex;
    gap:calc(100%/6);
    flex-wrap: wrap;
    text-align: center;
    justify-content: space-evenly;
}
.skillWrap .imgBox,
.designImg{
    width: 200px;
}
.box{
    position: relative;
}
.skillWrap .imgBox{
    background-color: #fff;
    border-radius: 20px;
    margin: 20px 0;
    filter: drop-shadow(2px 4px 6px #ccc);
    position: relative;
}
.skillWrap .imgBox img{
    transition: all .5s;
}
.skillWrap .imgBox>span,
.designImg>span{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    font-size: 3.5em;
    font-weight: bold;
    color: yellowgreen;
    opacity: 0;
    transition: all .5s;
}
.skillWrap .designImg img{
    transition: all .5s;
}
.skillWrap .imgBox:hover img{
    opacity: 0;
}
.skillWrap .designImg:hover img{
    filter: brightness(0.5);
}
.skillWrap .imgBox:hover span
,.skillWrap .designImg:hover span{
    opacity: 1;
}
.skillWrap .designImg:hover span{
    color:#fff;
}
.skillWrap img{
    width: 100%;
    transform: scale(0.9);
}
.skillWrap .textBox>strong{
    font-size: 1.8em;
}
/* Front.js */
.subTitle>h2{
    font-size: 30px;
    padding: 20px 0;
    animation-name: rightIn;
    animation-iteration-count: inherit;
    animation-duration: 1s;
}
.skillTitle{
    margin-bottom: 50px;
}
.frontExplain{
    display: flex;
    justify-content: space-evenly;
    padding: 30px;
}
.frontExplain h2{
    padding: 10px 0;
}
.frontExplain>li{
    background: #fff;
    filter: drop-shadow(0px 0px 5px #ccc);
    width: 29%;
    padding: 10px;
    text-align: center;
    animation-name: fadeIn;
    animation-iteration-count: inherit;
    animation-duration: 3s;
}
.subExplain{
    text-align: center;
    line-height: 2;
}