.portfolioWrap{
    text-align: center;
}
.slick-list{
    top: -20px;
}
.slick-arrow{
    position: absolute;
    top: 40%;
    z-index: 1000;
    border: none;
    width: 60px;
    height: 60px;
    text-indent: -9999px;
    cursor: pointer;
    border-radius: 50%;
    transition: all .3s;
}
.slick-arrow:hover{ 
  background-color: yellowgreen;
}
.slick-prev{
    left:60px;
    background: #eee url(../images/left_arrow_icon.png) no-repeat center center;
    background-size: 30px 30px;
}
.slick-next{
    right: 60px;
    background: #eee  url(../images/arrow_right_icon.png) no-repeat center center;
    background-size: 30px 30px;
}
.slick-slide img {
    display: inline-block;
    filter: drop-shadow(0 0 2px white);
    width: 70%;
}
.slick-dots{
    position: absolute;
    bottom: 50px;
    right: 50%; 
    transform: translateX(50%);

}
.slick-dots>li{
    display: inline;
    padding: 0 10px;
}
.slick-dots>li>button{
    border-radius: 50%;
    border: none;
    background-color: #eee;
    color: #333;
    width: 20px;
    height: 20px;
    cursor: pointer;
}
li.slick-active>button{
    background-color: #000;
    color: #fff;
    width: 25px;
    height: 25px;
}
.titleBox_port{
    text-align: center;
    color: #666;
    animation-name: fadeIn;
    animation-iteration-count: inherit;
    animation-duration: 1s;
}
.titleBox_port>h2{
    font-size: 30px;
}
.titleBox_port>p{
    font-size: 1.7em;
    color: #f6c915;
}
.gitLink{
    padding: 5px 0;
}
.gitLink>h2{
    font-size: 2em;
    padding: 10px 0;
}
.gitLink>p{
    font-size: 1.5em;
    padding: 30px 0;
}
.gitLink>a{
    text-decoration: none;   
    color: #666;
    font-size: 1.3em;
    background-color: #fff;
    margin: 0 5px;
    padding: 10px 20px;
    border-radius: 20px;
    filter: drop-shadow(0 0 3px #ccc);
    transition: all .3s;
}
a.githubLink{
    padding: 10px 10px;
}
a.githubLink>img{
    width: 25px;
    padding-right: 5px;
    border-radius: 50%;
    vertical-align: middle;
}
.gitLink a.githubLink:hover{
    filter: drop-shadow(0 0 3px transparent);
    background-color: #000;
    color: #fff;
}
.gitLink a:hover{
    filter: drop-shadow(0 0 3px transparent);
    background-color: yellowgreen;
    color: #fff;
}
.gitLink span{
    font-size: 0.8em;
    color: #222;
    margin: 0 4px;
    padding: 7px 10px;
    background-color: #f6c915;
    border-radius: 20px;
}
.gitLink span.contribution{
    border: 2px solid #f6c915;
    background-color: #fff;
}
.proImg{
    transform: scale(0.8);
}
.proImg>h1{

}
.proImg>h2>span{
    font-size: 18px;
}
.deviceIcon{
    display: inline-block;
    width: 40px;
    height: 40px;
    margin: 0 5px;
    background-color: #fff;
    border-radius: 50%;
    border: 1px solid #222;
}
.deviceIcon>img{  
    width: 100%;
    height: 100%;
    transform: scale(0.75);
}