
.aboutWrap {
    display: flex;
    gap: 50px;
    padding: 50px 0 0;
    color: #222;
}
.titleBox_about>h2,
.titleBox_about>strong,
.titleBox_about>p{
    animation-name: rightIn;
    animation-iteration-count: inherit;
}
.titleBox_about>h2{
    font-size: 40px;
    animation-duration: 1s;
}

.titleBox_about>strong{
    color: yellowgreen;
    font-size: 1.7em;
    line-height: 3;
    animation-duration: 1.5s;
}
.titleBox_about>p{
    font-size: 1.2em;
    line-height: 1.5;
    word-break: keep-all;
    color: #6e6e6e;
    width: 80%;
    animation-duration: 1.8s;
}
.aboutWrap .bgImg{
    width: 600px;
    height: 100%;
    animation-name: fadeIn;
    animation-iteration-count: inherit;
    animation-duration: 1.8s;
}
.aboutWrap .bgImg img{
    width: 100%;
    height: 100%;
    filter: brightness(1.2);
}
.aboutWrap .box{
    background-color: #fff;
    color: #222;
    display: flex;
    border-radius: 10px;
    margin: 0 0 5px;
    padding: 15px;
    column-gap: 20px;
    align-items: center;
    width: 400px;
    filter: drop-shadow(2px 4px 6px #ccc);
}
.aboutWrap .box:hover{
    position: relative;
    right: 40px;
}
.aboutWrap .box img{
    width: 50px;
}
.aboutWrap .box strong{
    font-size: 1.5em;
}